    margin: 0
}

td,
th {
    margin: 0
}

html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

a img,
body,
fieldset,
img {
    border: 0
}

input[type=email],
input[type=tel],
input[type=text],
textarea {
    -webkit-appearance: none
}

a,
a:focus,
button,
div,
h1,
h2,
h3,
h4,
h5,
h6,
input,
select,
span,
textarea {
    outline: 0
}

ol,
ul {
    list-style-type: none
}

table {
    border-spacing: 0;
    border-collapse: collapse;
    margin: 0 auto 50px;
    width: 50%
}

*,
:after,
:before {
    -webkit-box-sizing: inherit;
    box-sizing: inherit
}

@media (prefers-reduced-motion:reduce) {
    * {
        -webkit-transition: none!important;
        -o-transition: none!important;
        transition: none!important
    }
}

body {
    background: #fff;
    margin: 0;
    font-family: Arial, sans-serif;
    background: #f7f8f9
}

header {
    position: fixed;
    top: 0;
    width: 100%;
    background: #ffde28;
    min-height: 20px;
    padding: 3px 10px;
    text-align: center;
    display: flex;
    justify-content: space-between;
}

.search-input {
    width: 140px;
}

select {
    margin: 0 1px;
}

.main-title {
    margin: 50px 0;
    text-align: center;
}



.headrControler{
    display: flex;
    align-items: baseline;
}

.iconButton{
  padding: 7px 5px;
  text-align: center;
  text-decoration: none;
  margin: 0px 2px;
  cursor: pointer;
  display: inline-block;
  vertical-align: top;
  width: 25px;
  box-sizing: border-box;
}

 .gg-layout-list {
 transform: scale(var(--ggs,1))
}

.gg-layout-list,
.gg-layout-list::after,
.gg-layout-list::before {
 box-sizing: border-box;
 position: relative;
 display: block;
 width: 10px;
 height: 2px;
 border-left: 6px double;
 border-right: 4px solid
}

.gg-layout-list::after,
.gg-layout-list::before {
 content: "";
 position: absolute;
 left: -6px
}

.gg-layout-list::after {
 bottom: 4px
}

.gg-layout-list::before {
 top: 4px
}

 .gg-layout-grid-small {
 transform: scale(var(--ggs,1))
}

.gg-layout-grid-small,
.gg-layout-grid-small::after,
.gg-layout-grid-small::before {
 box-sizing: border-box;
 position: relative;
 display: block;
 width: 10px;
 height: 2px;
 border-left: 6px double;
 border-right: 2px solid
}

.gg-layout-grid-small::after,
.gg-layout-grid-small::before {
 content: "";
 position: absolute;
 left: -6px
}

.gg-layout-grid-small::after {
 bottom: 4px
}

.gg-layout-grid-small::before {
 top: 4px
} 
 
.container {
    max-width: 940px;
    margin: 0 auto;
    padding: 0 1%;
}

.grid{
    display: grid;
    grid-template-columns: repeat(4, 2fr);
    grid-gap: 14px;
}


.list {
    display: block;
}

.goods-cell {
    box-sizing: border-box;
    text-align: center;
    border: 1px solid gray;
    width: 100%;
    min-height: 200px;
    padding: 0px 1%;
    /* margin: 5px */
}

.goods-list {
    max-height: 40px;
    display: flex;
    border: 1px solid gray;
    justify-content: flex-start;
    line-height: 30px;
    padding: 0px 1%;
}

.goods-list__img {
    width: 30px;
    height: 30px;
    object-fit: fill;
    margin: 0 5px 0 5px;
}

.goods-cell__img {
    width: 100%;
    height: auto;
    object-fit: fill;
}



@media only screen and (max-width:700px) {
    .container {
        padding: 0 1%;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 4px;
    }
}

@media only screen and (max-width:500px) {
    .container {
        padding: 0 1% 1% 1%;
        grid-template-columns: repeat(2, 1fr);
        
    }
}


